.numberSquare {
  width: 24%;
  height: 24%;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: min(20vw, 20vh);
  color: rgba(255, 255, 255, 0.5);
  text-shadow: 2px 2px rgba(100, 100, 100, 0.1);
  animation-duration: 0.2s;
  user-select: none;
  transition: opacity 2s;
  margin: 0.5%;
}

.numberSquare.empty {
  background: rgba(0, 0, 0, 0);
}

.numberSquare.inPlace {
  margin: 0;
  width: 25%;
  height: 25%;
}

.numberSquare.sorted {
  opacity: 0;
}

.numberSquare.row-0 {
  transform: rotate(45deg);
}

.numberSquare.slideLEFT {
  animation-name: slide-from-right;
}
.numberSquare.slideDOWN {
  animation-name: slide-from-above;
}
.numberSquare.slideRIGHT {
  animation-name: slide-from-left;
}
.numberSquare.slideUP {
  animation-name: slide-from-below;
}

@keyframes slide-from-right {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}
@keyframes slide-from-above {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes slide-from-left {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}
@keyframes slide-from-below {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0);
  }
}
